@import url('https://fonts.googleapis.com/css2?family=Prompt&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Prompt&family=Sriracha&display=swap');


*{
  box-sizing: border-box;
  font-family: 'Prompt', sans-serif;

}

body{
  background: #f7f7ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
}

.container{
  margin: 30px auto;
  width: 350px;

}

.horizontal-menu{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: black;
  text-align: center;
}

.horizontal-menu li{
  display: inline-block;
}

.horizontal-menu a{
  color: #f7f7ff;
  text-decoration: none;
  padding: 16px;
  display: block;

}